@import url(https://fonts.googleapis.com/css?family=Lora);
#content {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 100vh;
  background: #642b73;
  background: linear-gradient(to bottom, #eaf2f0, #418583);
}
#quote-box {
  padding: 0;
}

blockquote {
  margin: 3.7em auto;
  padding: 2em;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background: linear-gradient(white, white) padding-box,
    url(https://picsum.photos/id/1019/200/300/?blur=2) border-box 0 / cover;
  border: 2em solid transparent;
  box-shadow: 5px 3px 30px black;
  font-size: 1.4em;
  font-family: "Lora", serif;
  font-style: italic;
  line-height: 1.5;
  max-width: 80%;
}
footer a {
  list-style: none;
  color: #000;
}
#link {
  color: #000;
}
#btn-container {
  padding: 20px;
}
#tweet-quote {
  padding: 2px;
  margin: 0px;
}
#btn-new-qoute {
  padding: 2px;
  margin: 0px;
}

footer {
  padding-top: 1.3em;
}

cite {
  font-style: normal;
  font-size: 1em;
  font-weight: bold;
}

@media only screen and (min-width: 600px) {
  blockquote {
    margin: 3.7em auto;
    padding: 2em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background: linear-gradient(white, white) padding-box,
      url(https://picsum.photos/id/1019/200/300/?blur=2) border-box 0 / cover;
    border: 2em solid transparent;
    box-shadow: 5px 3px 30px black;
    font-size: 1.4em;
    font-family: "Lora", serif;
    font-style: italic;
    line-height: 1.5;
    max-width: 80%;
  }
  footer a {
    list-style: none;
    color: #000;
  }
  #link {
    color: #000;
  }
  #btn-container {
    padding: 20px;
  }
  #tweet-quote {
    padding: 2px;
    margin: 0px;
  }
  #btn-new-qoute {
    padding: 2px;
    margin: 0px;
  }

  footer {
    padding-top: 1.3em;
  }

  cite {
    font-style: normal;
    font-size: 1em;
    font-weight: bold;
  }
}





